<template>
  <el-menu
    :default-active="activeIdx"
    class="el-menu-header"
    mode="horizontal"
    @select="handleSelect"
    :background-color="headStyle.hdBgColor"
    :text-color="headStyle.hdTextColor"
    :active-text-color="headStyle.hdActiveTextColor"
  >
    <!-- logo Start -->
    <el-menu-item class="logo-container" disabled index='main'>
      <logo-wrap />
    </el-menu-item>
    <!-- logo End -->
    <!-- subApp Start -->
    <template>
      <el-menu-item
        v-for="(item,idx) of pressionSubAppRoutes"
        :index="`${idx}`"
        :key="item.pressionCode"
        >{{ item.zhName }}</el-menu-item
      >
    </template>
    <!-- subApp End -->
    <!-- avatar Start -->
    <el-menu-item class="avatar-wrap" disabled index='main'>
      <avatar-wrap />
    </el-menu-item>
    <!-- avatar End -->
  </el-menu>
</template>
<script>
import {headStyleVar} from '@/common/scss/index'
import { LogoWrap, AvatarWrap } from "./modules";
import { SUB_APP_ROUTERS } from "@/config/subAppRouters";

/* mock 子应用路由权限码 */
const pressionList = ["1", "2","3","4"];
export default {
  name: "header-nav",
  components: {
    LogoWrap,
    AvatarWrap
  },
  data() {
    return {
      activeIdx: ''
    };
  },
  computed: {
    //headStyle
    headStyle:function() {
      return headStyleVar
    },
    // 根据权限过滤子系统目录
    pressionSubAppRoutes: function() {
      return SUB_APP_ROUTERS.filter(item => pressionList.includes(item.pressionCode));
    },
    activeSubRoute:function() {
      return this.pressionSubAppRoutes[this.activeIdx]
    }
  },
  watch:{
    '$route':function(val) {
      this.handleRoute(val)
    }
  },  
  mounted() {    
    this.handleRoute(this.$route)
  },
  methods: {
    handleSelect(idx) {
      this.activeIdx = idx
      this.$router.push(this.activeSubRoute.activeRule)
    },
    handleRoute(route) {
      let {path} = route
      let regx = /^\/sub\/([\w]+)/i; 
      let subPath = path.match(regx) //refresh 初始化activeIdx
      if(path.indexOf('/sub/')!==-1){
        this.activeIdx = `${pressionList.indexOf(subPath[1])}`
      }else {
        this.activeIdx = ''
      }
    }
  }
};
</script>
<style lang="scss" scoped>
li.logo-container {
  width: 210px;
  user-select: none;
  &:hover {
    background: none !important;
  }
}
li.avatar-wrap {
  float: right;
  user-select: none;
  &:hover {
    background: none !important;
  }
}

li.logo-container.is-disabled,li.avatar-wrap.is-disabled{
  opacity: 1;
  cursor: pointer;
  background: none;
}
</style>
